import React, { useState } from 'react';
import './App.styles.css';
import Content from './Content';
import { ArticleDataArray } from './ArticleDataArray';
import BlogHeader from './BlogHeader';

export type articleData = {
  title: string;
  reflection: string;
  photo: string;
  photoAltText: string;
  photoCaption: string;
}

export default function App() {
  const [articleNumber, changeArticleNumber] = useState(0);
  function changeArticle (newNumber: number) {
    changeArticleNumber(newNumber);
  }

  return (
    <div>
      <BlogHeader />
      <Content articleNumber={articleNumber} buttonFunction={changeArticle} {...{ ...ArticleDataArray[articleNumber] }}/>
    </div>
  );
}
