import React from 'react';
import './NavigationBar.styles.css';
import NavigationButton from './NavigationButton';
import Flexbox from 'flexbox-react';
import { ArticleDataArray } from './ArticleDataArray';

export type navBarProps = {
    buttonFunction: Function;
}

export default function NavigationBar ({buttonFunction}: navBarProps) {
    return (
        <div className='navBarContainer'> 
            <Flexbox flexDirection='column'>
                <NavigationButton text={ArticleDataArray[0].title} buttonFunction={buttonFunction} articleNumber={0} />
                <NavigationButton text={ArticleDataArray[1].title} buttonFunction={buttonFunction} articleNumber={1} />
                <NavigationButton text={ArticleDataArray[2].title} buttonFunction={buttonFunction} articleNumber={2} />
                <NavigationButton text={ArticleDataArray[3].title} buttonFunction={buttonFunction} articleNumber={3} />
                <NavigationButton text={ArticleDataArray[4].title} buttonFunction={buttonFunction} articleNumber={4} />
                <NavigationButton text={ArticleDataArray[5].title} buttonFunction={buttonFunction} articleNumber={5} />
                <NavigationButton text={ArticleDataArray[6].title} buttonFunction={buttonFunction} articleNumber={6} />
                <NavigationButton text={ArticleDataArray[7].title} buttonFunction={buttonFunction} articleNumber={7} />
                <NavigationButton text={ArticleDataArray[8].title} buttonFunction={buttonFunction} articleNumber={8} />
                <NavigationButton text={ArticleDataArray[9].title} buttonFunction={buttonFunction} articleNumber={9} />
                <NavigationButton text={ArticleDataArray[10].title} buttonFunction={buttonFunction} articleNumber={10} />
                <NavigationButton text={ArticleDataArray[11].title} buttonFunction={buttonFunction} articleNumber={11} />
                <NavigationButton text={ArticleDataArray[12].title} buttonFunction={buttonFunction} articleNumber={12} />
                <NavigationButton text={ArticleDataArray[13].title} buttonFunction={buttonFunction} articleNumber={13} />
                <NavigationButton text={ArticleDataArray[14].title} buttonFunction={buttonFunction} articleNumber={14} />
            </Flexbox>
        </div>
    );
}