import React from 'react';
import Flexbox from 'flexbox-react';
import Article from './Article';
import NavigationBar from './NavigationBar';
import { articleData } from './App';

export type contentProps = articleData & {
    buttonFunction: Function;
    articleNumber: number
}

export default function Content ({title, reflection, photo, photoAltText, photoCaption, buttonFunction, articleNumber}: contentProps ) {
    return (
        <div>
            <Flexbox flexDirection='row'>
                <NavigationBar buttonFunction={buttonFunction} />
                <Article title={title} reflection={reflection} photo={photo} photoAltText={photoAltText} photoCaption={photoCaption} articleNumber={articleNumber}/>
            </Flexbox>
        </div>
    );
}