import React from 'react';
import "./NavigationButton.styles.css";

export type buttonProps = {
    text: string;
    buttonFunction: Function;
    articleNumber: number;
}

export default function NavigationButton ({text, buttonFunction, articleNumber}: buttonProps) {
    return (
        <button onClick={() => buttonFunction(articleNumber)} className="navButton"> 
            <p className='buttonText'> {text} </p>
        </button>
    );
}