import React from 'react';
import './Article.styles.css';
import { articleData } from './App';

export type ArticleProps = articleData & {
    articleNumber: number;
}

export default function Article ({title, reflection, photo, photoAltText, photoCaption, articleNumber}: ArticleProps) {
    return (
        <div className='articleContainer'>
            <div className='imageContainer'>
                <img src={photo} alt={photoAltText} className='articleImage'/>
                <p className='imageCaption'> {photoCaption} </p>
            </div>
            <h2 className='articleTitle'>{title}</h2>
            <p className='articleBody'> {reflection} </p>
            {(articleNumber === 0) ? 
                <p>For more information about the Inspire program, check out their website here: <a href="https://www.inspireuvic.org/">https://www.inspireuvic.org/</a> <br></br>
                For more information about the ClimAct project, visit this link: <a href="https://www.inspireuvic.org/climact">https://www.inspireuvic.org/climact</a> </p> 
                : false} 
        </div>
    )
}